// 确认信息
<template>
  <div id="informationConfirmation">
    <div class="box">
      <van-form @submit="onSubmit" :show-error-message="false">
        <section>
          <div class="section_title"><span></span> <span>科目信息</span></div>
          <van-field v-model="username" readonly name="报考点" label="报考点" />
          <van-field v-model="username" readonly name="考点" label="考点" />
          <van-field
            v-model="username"
            readonly
            name="考点编号"
            label="考点编号"
          />
          <van-field
            v-model="username"
            readonly
            name="报考科目"
            label="报考科目"
          />
          <van-field
            v-model="username"
            readonly
            name="报考级别"
            label="报考级别"
          />
          <van-field v-model="username" readonly name="辅导课" label="辅导课" />
          <van-field
            v-model="username"
            readonly
            name="备注信息"
            label="备注信息"
          />
        </section>
        <section>
          <div class="section_title">
            <span></span> <span>证书邮寄方式</span>
          </div>
          <van-field v-model="username" readonly name="联系人" label="联系人" />
          <van-field
            v-model="username"
            readonly
            name="手机号码"
            label="手机号码"
          />
          <van-field
            v-model="username"
            readonly
            name="所在地区"
            label="所在地区"
          />
        </section>
        <section>
          <div class="section_title"><span></span> <span>报考信息</span></div>
          <van-field
            v-model="username"
            readonly
            name="考试费用"
            label="考试费用"
          />
          <van-field
            v-model="username"
            readonly
            name="订单状态"
            label="订单状态"
          />
        </section>

        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            >立即报名</van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "informationConfirmation",
  components: {},
  props: {},
  data() {
    return {
      username: "",
      password: "",
    };
  },
  created() {},
  mounted() {},
  activated() {},
  update() {},
  methods: {},
  filters: {},
  computed: {},
  watch: {},
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>





